.sidebar {
  width: 215px;
  padding: 35px 10px;
  background-color: #5997c3;

  .sidebar-link {
    display: block;
    margin-bottom: 15px;
    width: 100%;
    padding: 15px;
    background-color: transparent;
    border-radius: 10px;
    cursor: pointer;
    color: #FFF;
    transition: background-color .2s;

    &:last-child {
      margin-bottom: 0;
    }

    &.active, &:hover {
      background-color: #8ab6d5;
    }

    &:active {
      background-color: #7ca5c1;
    }
  }
}
