.header {
  display: inline-flex;
  justify-content: space-between;
  flex: 0 0 95px;
  width: 100%;
  padding: 20px;
  background-color: #d9e8f2;
}

.header__logo {
  display: flex;
  align-items: center;
  width: 195px;
  height: 100%;
  border-right: 1px solid #707070;

  & img {
    height: 30px;
  }
}

.quick-links {
  display: flex;
  align-items: center;
}

.quick-links__button {
  display: inline-flex;
  align-items: center;
  background-color: #5997c3;
  color: #FFF;
  padding: 15px 30px;
  margin: 0 5px;
  border-radius: 5px;
}

.user-actions {
  display: inline-flex;
}

.user-actions__item {
  position: relative;
  display: flex;
  align-items: center;
  padding: 15px;
  border-right: 1px solid #707070;
  color: #707070;

  .notification {
    position: absolute;
    top: 12px;
    left: 30px;
    width: 15px;
    height: 15px;
    text-align: center;
    border-radius: 100%;
    background: #F00;
    color: #FFF;
    font-size: 12px;
    padding-top: 1px;
  }

  .icon {
    height: 30px;
    margin-right: 15px;
  }

  img {
    height: 30px;
    color: #707070;
  }

  &:last-child {
      border-right: 0;
  }
}
